
.container.fluid {
    padding: 0;
}

:root {
  --jumbotron-padding-y: 3rem;
}
.box-shadow {
  box-shadow: 0 .25rem .75rem rgba(0, 0, 0, .05);
}


.card-body {
}
footer {
    padding-top: 3rem;
    padding-bottom: 3rem;
    background: #2d2b40; /* fallback for old browsers */
    background: -webkit-linear-gradient(to bottom, #343A40, #000000); /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to bottom, #343A40, #000000); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
footer p {
    margin-bottom: .25rem;
}
.mcontent {
    min-height: 153px;
    padding: 20px;
    font-size: 1.3em;
}






















































































/* ---- isotope ---- */
.grid {
    border: 1px solid #333;
}

/* clear fix */
.grid:after {
    content: '';
    display: block;
    clear: both;
}
.isoDefault {
    min-height: 510px;
}

/* ---- .element-item ---- */
.element-item {
    position: relative;
    float: left;
    width: 200px;
    height: 300px;
    margin: 10px;
    padding: 10px;
    background: #ebeeec;
    color: #262524;
}
.element-item > * {
    margin: 0;
    padding: 0;
}










































.jumbotron .container {
  padding: 0;
}
.jumbotron-heading {
  color: bisque;
}
.jumbotron {
  padding-top: var(--jumbotron-padding-y);
  padding-bottom: var(--jumbotron-padding-y);
  margin-bottom: 0;
  background-color: #fff;
  background-image: url(/static/img/imagenCabecera.b50c95f.jpg);
}
@media (min-width: 768px) {
.jumbotron {
    padding-top: calc(var(--jumbotron-padding-y) * 2);
    padding-bottom: calc(var(--jumbotron-padding-y) * 2);
}
}
.jumbotron p:last-child {
  margin-bottom: 0;
}
.jumbotron-heading {
  font-weight: 300;
}
.jumbotron .container {
  max-width: 40rem;
}


.progress-bar {
    margin: 10px 0;
}
